export const toAbsoluteUrl = pathname => `https://cloud.tagbox.com/app/analytics` + pathname;

export const ordinal_suffix_of = (i) => {
    var j = i % 10,
        k = i % 100;
    if (j == 1 && k != 11) {
        return i + "st";
    }
    if (j == 2 && k != 12) {
        return i + "nd";
    }
    if (j == 3 && k != 13) {
        return i + "rd";
    }
    return i + "th";
}

export const secondsToTime = (secs) => {
    // var hours = Math.floor(secs / (60 * 60));
    // var divisor_for_minutes = secs % (60 * 60);
    // var minutes = Math.floor(divisor_for_minutes / 60);

    // var divisor_for_seconds = divisor_for_minutes % 60;
    // var seconds = Math.ceil(divisor_for_seconds);
    // return `${hours}:${minutes}:${seconds}`;

    var sec_num = parseInt(secs, 10); // don't forget the second param
    var hours = Math.floor(sec_num / 3600);
    var minutes = Math.floor((sec_num - (hours * 3600)) / 60);
    var seconds = sec_num - (hours * 3600) - (minutes * 60);

    if (hours < 10) { hours = "0" + hours; }
    if (minutes < 10) { minutes = "0" + minutes; }
    if (seconds < 10) { seconds = "0" + seconds; }

    return hours + ':' + minutes + ':' + seconds;


}

export const getTimeTypeSecondToTime = (secs, type) => {
    let dataTime = secondsToTime(secs);
    let acrtualTime = dataTime.split(':')
    if (type == 1) {
        if (acrtualTime && acrtualTime.length) return acrtualTime[0];
        else return 0;
    }
    else if (type == 2) {

        if (acrtualTime && acrtualTime.length) return acrtualTime[1];
        else return 0;
    }
    else if (type == 3) {
        if (acrtualTime && acrtualTime.length) return acrtualTime[2];
        else return 0;
    }
}
export const validURL = (str) => {
    var pattern = new RegExp('^(https?:\\/\\/)?' + // protocol
        '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|' + // domain name
        '((\\d{1,3}\\.){3}\\d{1,3}))' + // OR ip (v4) address
        '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
        '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
        '(\\#[-a-z\\d_]*)?$', 'i'); // fragment locator
    return !!pattern.test(str);
}

export const convertIntToBool = (value) => {
    if (value) return Boolean(value)
    else return value;

}


export const customClass = {
    confirmButton: 'btn-primary btn-min-w btn btn-primary',
    cancelButton: 'border-btn btn-outline-dark me-4 btn-min-w btn btn-primary',
}


export const updateURLForIframe = (url) => {
    if (url && url.includes('youtube.com')) {
        const urlSplits = url.split('?v=');
        if (urlSplits && urlSplits.length && urlSplits[1]) {
            url = `https://www.youtube.com/embed/${urlSplits[1]}`;
        }
    }
    else if (url && url.includes('vimeo.com')) {
        const urlSplits = url.split('/');
        if (urlSplits && urlSplits.length && urlSplits[urlSplits.length - 1]) {
            url = `https://player.vimeo.com/video/${urlSplits[urlSplits.length - 1]}?autoplay=1&loop=1&autopause=0`;
        }
    }
    return url;
}

export const getSecondsFromTime = (time) => {
    let actualTime = time.split(':');
    let totalSeconds = (+actualTime[0]) * 60 * 60 + (+actualTime[1]) * 60 + (+actualTime[2]);
    return totalSeconds;
}

export const loaderCircleStyle = { padding: 1, color: '#545454', fontSize: 10 }
export const getPosition = (partitions, currentSceneContent) => {
    const getPartitions = Object.keys(partitions).map((item) => ({ position: item, name: partitions[item] }))
    const filterData = getPartitions.filter((item) => item.name == currentSceneContent.name);
    return filterData;
}
export const GET_HEIGHT_WIDTH = (cssStyle, position, layouts, mixContentType) => {

    const actualLayout = layouts.filter((item) => item.id == mixContentType)

    if (actualLayout && actualLayout[0]) {


        const currentHeightWidth = GET_DYNAMIC_HEIGHT_WIDTH(cssStyle, position);
        const actualDefault = GET_DYNAMIC_HEIGHT_WIDTH(actualLayout[0].css, position);
        if (actualDefault && Object.keys(actualDefault).length) {
            if (parseInt(actualDefault.height) > parseInt(actualDefault.width)) {
                return { width: currentHeightWidth.width, height: 0, label: "Width" }
            }
            else if (parseInt(actualDefault.height) < parseInt(actualDefault.width)) {
                return { height: currentHeightWidth.height, width: 0, label: "Height" }
            }
            else {
                if (mixContentType == 1) {
                    return { height: currentHeightWidth.height, width: 0, label: "Height" }
                }
                else {
                    return { height: 0, width: 0 }
                }
            }
        }
        else {
            return currentHeightWidth;
        }
    }
}

const GET_DYNAMIC_HEIGHT_WIDTH = (cssStyle, position) => {
    const cData = cssStyle.split("#zone");

    var height = ""; var width = "";
    if (cData && cData.length) {
        cData.map((item, index) => {
            const trimContent = item.trim()
            const firstValue = trimContent.charAt(0);
            if (firstValue == position[0].position) {
                const replaceValue = item.split(";")
                if (replaceValue && replaceValue.length) {
                    replaceValue.map((rItem) => {
                        const getHeightWidth = rItem.split(":");
                        if (getHeightWidth && getHeightWidth[0] && getHeightWidth[0] == "height") height = getHeightWidth[1].replace("%", "");
                        if (getHeightWidth && getHeightWidth[0] && getHeightWidth[0] == "width") width = getHeightWidth[1].replace("%", "");
                    })
                }
            }
        })
    }
    return { height, width, }
}

export const convertHMS = (timeString) => {
    const arr = timeString.split(":");
    const seconds = arr[0] * 3600 + arr[1] * 60 + (+arr[2]);
    return seconds;
}




export const getRandomArbitrary = (min, max) => {
    return parseInt(Math.random() * (max - min) + min);
}

export const additionArrayData = (data) => {
    if (data && data.length > 0) {
        return data.reduce((total, num) => total + num)
    }
    else return 0
}

export const kFormatter = (value) => {
    // return Math.abs(num) > 999 ? Math.sign(num) * ((Math.abs(num) / 1000).toFixed(1)) + 'k' : Math.sign(num) * Math.abs(num)


    // return Math.abs(Number(num)) >= 1.0e+9 ? (Math.abs(Number(num)) / 1.0e+9).toFixed(2) + "B" : Math.abs(Number(num)) >= 1.0e+6 ? (Math.abs(Number(num)) / 1.0e+6).toFixed(2) + "M" : Math.abs(Number(num)) >= 1.0e+3 ? (Math.abs(Number(num)) / 1.0e+3).toFixed(2) + "K" : Math.abs(Number(num));
    try {


        var newValue = value;
        if (value >= 1000) {
            var suffixes = ["", "K", "M", "B", "T"];
            var suffixNum = Math.floor(("" + value).length / 3);
            var shortValue = '';
            for (var precision = 2; precision >= 1; precision--) {
                shortValue = parseFloat((suffixNum != 0 ? (value / Math.pow(1000, suffixNum)) : value).toPrecision(precision));
                var dotLessShortValue = (shortValue + '').replace(/[^a-zA-Z 0-9]+/g, '');
                if (dotLessShortValue.length <= 2) { break; }
            }
            if (shortValue % 1 != 0) shortValue = shortValue.toFixed(1);
            newValue = shortValue + suffixes[suffixNum];
        }
        return newValue;
    }
    catch (ex) {
        return Math.abs(value) > 999 ? Math.sign(value) * ((Math.abs(value) / 1000).toFixed(1)) + 'k' : Math.sign(value) * Math.abs(value)
    }

}
export const OverviewChartFormatting = (formattingData) => {
    let data = [];
    if (formattingData && formattingData.length > 0) {
        formattingData.map((item) => {
            data.push({
                name: item.name,
                type: 'line',
                data: dataConvertToInt(item.data)
            })
        })
        return data;
    }
    else return formattingData;
}

export const dataConvertToInt = (formateIntData) => {
    let updateData = [];
    if (formateIntData && formateIntData.length > 0) {
        formateIntData.map((item) => {
            updateData.push(parseInt(item))

        })
        return updateData


    }
    else return formateIntData

}


export const networkURLIsValid = (str) => {
    let output = str;
    let matches;
    matches = str.match(/(?:https?:\/\/)?(?:www.)?(?:twitter|medium|facebook|vimeo|instagram|google|pinterest|flickr|youtube|linkedin|vkontakte|vk|tiktok|soundcloud|giphy|airbnb|yammer|slack|yelp|workplace|rss|tumblr)(?:.com\/)?([@a-zA-Z0-9-_]+)/im);
    if (matches && matches.length > 0) return true
    else return false;
}

export const capitalizeFirstLetter = (string) => {
    return string.charAt(0).toUpperCase() + string.slice(1);
}

const UPDATE_INSTAGRAM_IMAGE_VIDEO = `https://${ENV}.taggbox.com/display/post/update`;
const NO_CAMERA_IMAGE = NoCameraImgPost;
const updateStatusRenewImage = (wallId, postId, table_name, e) => {
    const { moderation, authenticated } = store.getState();
    const { details, listData } = moderation;
    const { userData } = authenticated;
    const postData = listData.length > 0 ? listData.filter((val) => val.id == postId) : []
    const url = details?.wall?.url;
    const table = userData.UserPlan.db_table;
    let dataFormatted = {
        wallId: wallId,
        postId: postId,
        table_name: table,
        ownerId: localStorage.getItem('ownerId'),
        feedId: postData.length > 0 ? postData[0].feedId : '',
        type: postData.length > 0 ? postData[0].type : '',
        link: postData.length > 0 ? postData[0].link : '',
        postFile: postData.length > 0 ? postData[0].postFile : '',
        mediaFile: postData.length > 0 ? postData[0].mediaFile : '',
        url: url
    }
    axios.post(INSTAGRAM_IMAGE_VIDEO_UPDATE, dataFormatted).then(({ data }) => {
        e.target.src = data.media;
        e.target.attributes["data-load"].value = "1";
    })
}

export const updateInstagramPostImage = (data) => {
    const { authenticated } = store.getState();
    const { userData } = authenticated;
    const table = userData.UserPlan.db_table;

    let dataFormatted = {
        ...data,
        table_name: table
    }
    axios.post(`${UPDATE_INSTAGRAM_IMAGE_VIDEO}`, dataFormatted)
        .then((response) => { })
        .catch((error) => {
            console.error(error);
        });
};

const FETCH_LINK = (fetchUrl, postId, dataSrc, type) => GET_INSTAGRAM_IMAGE(fetchUrl).then((response) => {
    let html = response.data;
    if (html) {
        var regex = /_sharedData = ({.*);<\/script>/m,
            json = JSON.parse(regex.exec(html)[1]);
        if (typeof json.entry_data.PostPage[0].graphql.shortcode_media.edge_sidecar_to_children !== 'undefined') {
            if (type == 'video') {
                const videoPath = json.entry_data.PostPage[0].graphql.shortcode_media.edge_sidecar_to_children.edges.filter((item) => item.node.shortcode == postId[4])[0].node.video_url
                return videoPath;
            } else {
                const imagePath = json.entry_data.PostPage[0].graphql.shortcode_media.edge_sidecar_to_children.edges.filter((item) => item.node.shortcode == postId[4])[0].node.display_url
                return imagePath;
            }
        }
        else if (json.entry_data.PostPage[0].graphql.shortcode_media.__typename === 'GraphVideo') {

            if (json.entry_data.PostPage[0].graphql.shortcode_media.is_video) {
                return { video_url: json.entry_data.PostPage[0].graphql.shortcode_media.video_url, display_url: json.entry_data.PostPage[0].graphql.shortcode_media.display_url }
            }
        }
        else {
            return json.entry_data.PostPage[0].graphql.shortcode_media.display_url;
        }
    }

}).catch((error) => {
    if (dataSrc.includes("cdn.taggbox.com")) return NO_CAMERA_IMAGE;
    else return NO_CAMERA_IMAGE;
})


export const IMG_LOAD_AFTER_COMPLETE_LAYOUT = async (e) => {
    // return
    e.persist()
   
    const { userData } = store.getState().authenticated;
    const table_name = userData.UserPlan.db_table;
    const network = e.target.getAttribute("data-network")
    const wallId = e.target.getAttribute("data-wall-id");
    const postId = e.target.getAttribute("data-item-id");
    const filterId = e.target.getAttribute("data-filter-id");
    let links = e.target.getAttribute("data-link");
    let image_update = '';
    let video_update = '';
    // const UserDetail = store.getState();
    if (network) {
        const dataSrc = e.target.src;
        if ((network == 2 || network == 18 || network == 3 || network == 28) && (dataSrc.includes("cloud.taggbox.com") || dataSrc.includes("cdn.taggbox.com") || dataSrc.includes("cdninstagram.com") || dataSrc.includes("no-camera.svg") || dataSrc.includes("instagram-no-image") || network == 3 || network == 28) && e.target.getAttribute("data-load") == 0 && filterId != "34") {
            if ((network == 2 || network == 18)) {
                let postLinkId = links.split('/');
                let fetchUrl = `https://images${~~(Math.random() * 3333)}-focus-opensocial.googleusercontent.com/gadgets/proxy?container=none&url=${links}`;
                let IMG_URL_UPDATE = await FETCH_LINK(fetchUrl, postLinkId, dataSrc, 'image')
                if (IMG_URL_UPDATE && IMG_URL_UPDATE != null && IMG_URL_UPDATE.length > 0 && typeof IMG_URL_UPDATE === 'string' && (IMG_URL_UPDATE.includes("no-camera.svg") || IMG_URL_UPDATE.includes("lookaside.instagram.com"))) {
                    updateStatusRenewImage(wallId, postId, table_name, e)
                }
                else {
                    if (IMG_URL_UPDATE && IMG_URL_UPDATE != null && IMG_URL_UPDATE.length > 0 && typeof IMG_URL_UPDATE === 'string') {
                        e.target.src = `https://images.taggbox.com/${IMG_URL_UPDATE}`;
                        image_update = IMG_URL_UPDATE;
                        e.target.attributes["data-load"].value = "1";
                        if (IMG_URL_UPDATE.includes("no-camera.svg")) {
                            e.target.style = "visibility:visible!important;width:180px;margin:20px auto;object-fit: contain;";
                        }
                        e.target.style = "";
                    }
                    else {
                        if (IMG_URL_UPDATE.display_url && IMG_URL_UPDATE.display_url.includes("lookaside.instagram.com")) {
                            updateStatusRenewImage(wallId, postId, table_name, e)
                        }
                        else {
                            e.target.src = `https://images.taggbox.com/${IMG_URL_UPDATE.display_url}`;
                            image_update = IMG_URL_UPDATE.display_url;
                            video_update = IMG_URL_UPDATE.video_url;
                            e.target.attributes["data-load"].value = "1";
                            if (IMG_URL_UPDATE.display_url.includes("no-camera.svg")) {
                                e.target.style = "visibility:visible!important;width:180px;margin:20px auto;object-fit: contain;";
                            }
                            e.target.style = "";
                        }
                    }
                    if (!image_update.includes("no-camera.svg") && !image_update.includes("cdn.taggbox.com") && !image_update.includes("lookaside.instagram.com")) {

                        updateInstagramPostImage({
                            ownerId: localStorage.getItem('ownerId'),
                            wallId: wallId,
                            postFile: image_update,
                            video: video_update,
                            type: 2,
                            postId: postId,
                            table_name: table_name
                        });
                    }
                }

            }
            else {
                const { details, listData } = store.getState().moderation;
                const postData = listData.length > 0 ? listData.filter((val) => val.id == postId) : []
                const url = details?.wall?.url;

                let dataFormatted = {
                    wallId: wallId,
                    postId: postId,
                    table_name: table_name,
                    ownerId: localStorage.getItem('ownerId'),
                    feedId: postData.length > 0 ? postData[0].feedId : '',
                    type: postData.length > 0 ? postData[0].type : '',
                    link: postData.length > 0 ? postData[0].link : '',
                    postFile: postData.length > 0 ? postData[0].postFile : '',
                    mediaFile: postData.length > 0 ? postData[0].mediaFile : '',
                    url: url
                }
                axios.post(INSTAGRAM_IMAGE_VIDEO_UPDATE, dataFormatted).then(({ data }) => {
                    e.target.src = data.media;
                    e.target.attributes["data-load"].value = "1";

                })
            }
        }
        else {
            if (dataSrc.includes("no-camera.svg")) {
                e.target.style = "display:block;min-height:150px;width:80px;height:100%;margin:10px auto;object-fit: contain;";
            }
            else {
                e.target.src = NO_CAMERA_IMAGE;
                e.target.style = "display:block;min-height:150px;width:80px;height:100%;margin:10px auto;object-fit: contain;";

            }
            e.target.attributes["data-load"].value = "1";
        }

    }
    else {
        e.target.src = NO_CAMERA_IMAGE;
        e.target.style = "display:block;min-height:150px;width:80px;height:100%;margin:10px auto;object-fit: contain;";

    }

}


export const removeHttp = (str) => {
    return str.split('//')[1].replace(/\/$/, "").replace(/^www\./, '')
}


export const dateFormat = (date) =>{
    return moment(date).format("YYYY-MM-DD HH:mm:ss")
}


 