import moment from "moment";
import { Link } from "react-router-dom";

const FilterModeration = (props) => {

  const { children, dateFilter, selectedWallFeed, link, isClass } = props  

  return (
    <Link className={`${isClass ? isClass : `mb-4 last-child-mb-0 d-block`}`} to={selectedWallFeed.length ? `/moderation/index/${localStorage.getItem('wallId')}?${link}/${moment(dateFilter.startDate).format("YYYY-MM-DD")}/${moment(dateFilter.endDate).format("YYYY-MM-DD")}` : `#`}>
        {children}
    </Link>
  );
}

export default FilterModeration
